.header__container {
  /* display: none; */
  width: 100vw;
  height: 230px;
  padding-top: 45px;
  padding-bottom: 45px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
  background-image: linear-gradient($for-grad-header, $for-grad-header),
    url(../partials/img/Rectangle_x1.jpg);
  background-size: cover;
  @media screen and (min-width: $tablet) {
    padding-top: 40px;
    padding-bottom: 100px;
    padding-left: 75px;
    padding-right: 75px;
    background-image: linear-gradient($for-grad-header, $for-grad-header),
      url(../partials/img/Rectangle_tabl_x1.jpg);
    @media screen and (min-width: $desktop) {
      background-image: linear-gradient($for-grad-header, $for-grad-header),
        url(../partials/img/Rectangle_desc_x1.jpg);
    }
  }
}

.navi__box {
  display: flex;
  justify-content: space-between;
}

.navi__logoblock {
  display: flex;
}

.navi__logo {
  display: none;
  @media screen and (min-width: $tablet) {
    display: block;
    margin-left: 12px;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 35px;
    color: $white-text-and-bg;
  }
}

.input {
  display: flex;
  margin-top: 38px;
  margin-left: auto;
  margin-right: auto;
  width: 276px;
  height: 20px;
  border-bottom: 1px solid $white-text-and-bg;
  background-color: transparent;
  font-style: normal;
  font-weight: 40;
  font-size: 14px;
  line-height: calc(16 / 14);
  color: $white-text-and-bg;
  @media screen and (min-width: $tablet) {
    width: 336px;
  }
  &::placeholder {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: calc(16 / 14);
    color: $white-text-and-bg;
  }
}

.navi__item {
  background-color: inherit;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: calc(14 / 12);
  text-transform: uppercase;
  color: $white-text-and-bg;
}

.navi__item--active {
  position: relative;
}

.navi__item--active::after {
  content: '';
  display: inline-block;
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 3px;
  background: $accent;
  border-radius: 2px;
}

.navi__item:last-child {
  margin-left: 40px;
}
